FCBH.docReady(() => {
    new VirtualSummitPartTwo();
});

class VirtualSummitPartTwo {
    constructor() {
        this.paymentForm = this.getPaymentForm()
        const isValid = this.validatePaymentForm(this.paymentForm)
        if(isValid){
            this.createHiddenInputs()
        }
    }

    validatePaymentForm({ currency_code, amounts, recurring_donation, pledge_time }) {   
        if (!currency_code || !amounts || !recurring_donation || !pledge_time) {
            window.location = "/vision-virtual-summit-2020";
            return false;
        }
        return true;
    }

    getPaymentForm() {
        return FCBH.getFromStorage(FCBH.GIVE_FORM) || {};
    }

    createHiddenInputs(){
        const form = document.querySelector('#wf-form-Vision-Virtual-Summit-Personal-Info-Form')

        const programInput = this.createProgramInput()
        const whenInput = this.createWhenInput()
        const amountInput = this.createAmountInput()

        form.prepend(programInput, whenInput, amountInput)
    }

    createProgramInput(){
        const programInput = document.createElement('input')
        programInput.type = 'hidden'
        programInput.name = 'program'
        programInput.id = 'program'
        programInput.value = this.paymentForm?.program?.project_title
        return programInput
    }

    createWhenInput(){
        const donateInput = document.createElement('input')
        donateInput.type = 'hidden'
        donateInput.name = 'donate-when'
        donateInput.id = 'donate-when'
        donateInput.value = this.paymentForm.pledge_time
        return donateInput
    }

    createAmountInput(){
        const amountInput = document.createElement('input')
        amountInput.type = 'hidden'
        amountInput.name = 'amount'
        amountInput.id = 'amount'
        amountInput.value = this.paymentForm?.amounts?.usdValue
        return amountInput
    }
}